import  HomeProfileSlots  from "./templates/HomeProfileSlots";
import UserProfilePage from "./templates/UserProfilePage";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";


function App() {

  return (
            <Router>
              <Switch>
                  <Route path="/Kyle">
                      <UserProfilePage/>
                  </Route>
                  <Route exact path="/">
                      <HomeProfileSlots/>
                  </Route>
              </Switch>
          </Router>
        )
}

export default App;
